
.page-wraper {
    -webkit-box-shadow: 0 0 25px -15px #000;
    box-shadow: 0 0 25px -15px #000;
    .page-header {
        padding: 19px 0;
        .container {
            width: 1200px;
            margin: auto;
            .main-nav {
                .main-nav__list {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
        }        
      }
    }
  }
}

  

  .site-list__item {
    width: 20%;
    font-family: 'PT Sans', sans-serif;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    list-style: none;
  }
  
  .site-list__item a:hover {
    color: #816554;
  }
  
  .site-list__item a {
    color: #000;
  }
  
  .site-list__item:nth-child(3) {
    margin-left: auto;
  }
  
  .site-list__item--active {
    color: #604e43;
  }
  
  .nav-logo {
    position: absolute;
    top: 0;
    left: 42%;
    z-index: 100;
  }